@charset 'utf-8';

.tui-pagination * {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
    overflow: hidden;
}

.tui-pagination {
    height: 40px;
    margin: 14px 0 12px;
    line-height: normal;
    text-align: center;
    font-size: 0;
    margin-bottom: 80px;
}

.tui-pagination .tui-page-btn {
    display: inline-block;
    position: relative;
    width: 40px;
    height: 100%;
    padding: 8px 0 6px;
    margin-left: 8px;
    color: #333;
    font-size: 12px;
    font-weight: normal;
    line-height: 2;
    text-decoration: none;
    vertical-align: middle;
    border-radius: 50%;
    border: 1px solid #ddd;
    border-width: 1px 0;
    transition: all var(--transition-dur-and-func);
}

.tui-pagination .tui-page-btn:hover {
    background-color: #f4f4f4;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
    background: #9bb537;
    border-color: #9bb537;
    cursor: default;
}

.tui-pagination .tui-is-selected:hover {
    background-color: #9bb537;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
    margin-left: 0;
}

.tui-pagination .tui-first-child,
.tui-pagination .tui-prev-is-ellip {
    border-left: 1px solid #ddd;
}

.tui-pagination .tui-last-child,
.tui-pagination .tui-next-is-ellip {
    border-right: 1px solid #ddd;
}

.tui-pagination .tui-first-child.tui-is-selected {
    border-left: 1px solid #9bb537 !important;
}

.tui-pagination .tui-first-child {
    margin-left: 0;
}

.tui-pagination .tui-last-child.tui-is-selected {
    border-right: 1px solid #9bb537;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    overflow: hidden;
    padding: 0;
    font-size: 0;
    line-height: 39px;
    white-space: nowrap;
    font-weight: normal;
    border: 1px solid #ddd;
    background-color: #9bb537;
}

.tui-pagination .tui-prev-is-ellip {
    border-right: 0;
}

.tui-pagination .tui-next-is-ellip {
    border-left: 0;
}

.tui-pagination .tui-first+.tui-prev,
.tui-pagination .tui-next+.tui-last {
    border-left: 0;
}

.tui-pagination .tui-next+.tui-last,
.tui-pagination .tui-first {
    margin-left: 0;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
    background: url(data:image/png;base64,) no-repeat 0 0;
    display: inline-block;
    vertical-align: middle;
    font-size: 0px;
}

.tui-pagination .tui-prev {
    margin-right: 14px;
    margin-left: 4px;
}

.tui-pagination .tui-next {
    margin-left: 14px;
    margin-right: 4px;
}

.tui-pagination .tui-ico-first {
    width: 11px;
    height: 10px;
    background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
    width: 6px;
    height: 10px;
    background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
    width: 6px;
    height: 10px;
    background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
    width: 11px;
    height: 10px;
    background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
    width: 10px;
    height: 2px;
    margin-top: 30%;
    background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled:hover {
    background: #fff;
    cursor: default;
}

.tui-pagination .tui-is-disabled.tui-first,
.tui-pagination .tui-is-disabled.tui-prev,
.tui-pagination .tui-is-disabled.tui-last,
.tui-pagination .tui-is-disabled.tui-next {
    background-color: #050505;
}

.tui-pagination span:first-child,
.tui-pagination button:first-child {
    margin-left: 0;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
    background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
    background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
    background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
    background-position: -13px -12px;
}

.tui-page-inputs {
    display: inline-block;
    vertical-align: top;
}

.tui-page-inputs .tui-page-input {
    display: inline-block;
    width: 10px;
    height: 12px;
    padding: 8px 0 6px;
    font-size: 12px;
    text-align: center;
    color: #333;
    border: 0;
    outline: none;
}

.tui-page-inputs .tui-page-current {
    color: #9bb537;
}

.tui-page-separator {
    display: inline-block;
    font-size: 12px;
    height: 12px;
    padding: 8px 0 6px;
    vertical-align: top;
}

@media screen and (min-width: 768px) {
    .tui-pagination {
        height: 44px;
    }

    .tui-pagination .tui-page-btn {
        width: 44px;
        margin-left: 10px;
        line-height: 2.4;
    }

    .tui-pagination .tui-first-child {
        margin-left: 0;
    }

    .tui-pagination .tui-first,
    .tui-pagination .tui-prev,
    .tui-pagination .tui-next,
    .tui-pagination .tui-last,
    .tui-pagination .tui-prev-is-ellip,
    .tui-pagination .tui-next-is-ellip {
        line-height: 42px;
    }

    .tui-pagination .tui-prev {
        margin-right: 24px;
        margin-left: 8px;
    }

    .tui-pagination .tui-next {
        margin-left: 24px;
        margin-right: 8px;
    }
}

@media screen and (min-width: 1200px) {
    .tui-pagination {
        padding-left: 290px;
    }
}